<template>
  <div class="wrapper wrapper-content">
    <div class="ibox">
      <div class="ibox-title">
        <h5>期刊论文-中文-详情</h5>
      </div>

      <div class="ibox-content">
        <form class="form-horizontal" @submit.prevent="submit" v-if="canShow">
          <div class="row">
            <public-form v-bind:dataKeys="dataKeys" v-bind:form="form"></public-form>
          </div>
          <div class="text-center row">
            <template v-if="canEdit()">
              <button v-if="canSubmit" type="submit" class="btn btn-primary">保存</button>
              <button v-if="!canSubmit" class="btn btn-default">保存</button>
              <b v-if="canLocking()" class="btn btn-warning" @click="locking">提交</b>
            </template>
            <b class="btn btn-white" @click="cancel">取消</b>
          </div>
        </form>
      </div>
    </div>

  </div>
</template>

<script type="text/ecmascript-6">
import { CODE, periodicalPaperChineseKeys } from '@/assets/constant'
import dataService from '@/assets/method/data'
import tabsService from '@/assets/template/tabs'
import Cache from '@/assets/cache'
import Service from '@/assets/service/service'
export default {
  name: 'periodicalPaperChineseDetails',
  data () {
    return {
      id: this.$route.params['id'],
      form: {},
      code: CODE.PeriodicalPaperChinese,
      dataKeys: periodicalPaperChineseKeys,
      canShow: false,
      canSubmit: true,
      user: Cache.getUser(),
      permissionName: 'academic_achievements'
    }
  },
  methods: {
    canEdit () {
      return Cache.canEdit(this.form, this.permissionName) || this.id == 'new'
    },
    canLocking () {
      return Cache.canLocking(this.form, this.permissionName)
    },
    locking () {
      if (!confirm('确定提交吗?提交后将不能修改!')) return
      dataService.getFormVal(this.dataKeys, this.form).then(result => {
        result['status'] = 1
        this.confirm(result)
      })
    },
    submit () {
      if (this.form['selectUnit666'] == '华师') {
        this.form['hua_shi'] = this.form['selectRemark666']
        this.form['other_unit'] = null
      } else {
        this.form['other_unit'] = this.form['selectRemark666']
        this.form['hua_shi'] = null
      }
      dataService.getFormVal(this.dataKeys, this.form).then(result => this.confirm(result))
    },
    confirm(result) {
      if (!this.canSubmit) return
      const request = {}
      for (const key in result) {
        if (key == 'selectUnit666' || key == 'selectRemark666') {
        } else {
          request[key] = result[key]
        }
      }
      this.canSubmit = false
      if (this.id != 'new') {
        Service.PeriodicalPaperChinese.edit(this.id, request).then(response => this.responseHandle(response))
      } else  {
        Service.PeriodicalPaperChinese.add(request).then(response => this.responseHandle(response))
      }
    },
    responseHandle (response) {
      if (response.code == 200) {
        toastr.success(response.msg)
        setTimeout(() => dataService.tabReload(202), 1000)
      } else {
        toastr.error(response.msg)
        this.canSubmit = true
      }
    },
    cancel () {
      tabsService.closeNowTab()
    }
  },
  created () {
    dataService.getTableKeys(Service, this.code, this.dataKeys).then(dataKeys => {
      this.dataKeys = dataKeys
      this.canShow = true
    })
    if (this.id != 'new') {
      Service.PeriodicalPaperChinese.details(this.id).then(response => {
        if (response.code == 200) {
          this.form = response.data
          if (this.form['hua_shi']) {
            this.form['selectUnit666'] = '华师'
            this.form['selectRemark666'] = this.form['hua_shi']
          } else {
            this.form['selectUnit666'] = '外单位'
            this.form['selectRemark666'] = this.form['other_unit']
          }
        }
      })
    }
  },
  mounted () {
  }
}
</script>

<style scoped>

</style>



